import logo from "../images/crizgames_logo.svg";
import unity from "../images/unity.svg";
import "../theme/customTheme.css";

import React from "react";
import PropTypes from "prop-types";
import { useStaticQuery, graphql, Link } from "gatsby";
import { Layout, Menu, Space } from "antd";
import { GithubFilled, MailOutlined } from "@ant-design/icons";
import LinkNewTab from "./LinkNewTab";

const { Header, Footer, Content } = Layout;

const Container = ({ defKey, children }) => {
  const data = useStaticQuery(graphql`
    query ContainerQuery {
      site {
        siteMetadata {
          title
        }
      }
    }
  `);

  return (
    <Layout style={{ backgroundColor: "transparent" }}>
      <Header
        style={{
          position: "absolute",
          top: "0",
          right: "0",
          left: "0",
          maxWidth: "1280px",
          marginRight: "auto",
          marginLeft: "auto",
          backgroundColor: "white",
          // TODO boxShadow: "",
        }}
      >
        <div style={{ margin: "0 auto" }}>
          {/* TODO: MAKE THIS BETTER */}
          <h1
            style={{
              float: "left",
            }}
          >
            <Link
              to="/"
              style={{
                backgroundColor: "transparent",
                color: "rgba(0,0,0,0.85)",
                fontSize: "18px",
                display: "flex",
              }}
            >
              <img
                src={logo}
                style={{
                  position: "relative",
                  marginRight: "16px",
                  height: "32px",
                  top: "16px",
                }}
                alt="logo"
              ></img>
              {data.site.siteMetadata.title}
            </Link>
          </h1>
          <Menu
            style={{
              backgroundColor: "transparent",
              float: "right",
            }}
            mode="horizontal"
            defaultSelectedKeys={defKey}
          >
            <Menu.Item key="1">
              <Link to="/assets/">Unity Assets</Link>
            </Menu.Item>
            {/* <Menu.Item key="2">
              <Link to="/projects/">Other Projects</Link>
            </Menu.Item> */}
            <Menu.Item key="3">
              <Link to="/mods/">Mods</Link>
            </Menu.Item>
            {/* <Menu.Item key="4">
              <Link to="/contact/">Support / Contact</Link>
            </Menu.Item> */}
          </Menu>
        </div>
      </Header>
      <Content
        style={{
          padding: "24px 50px",
          marginTop: 64,
          background: "transparent",
          minHeight: "100vh", //edit this to change minimum page height
        }}
      >
        <div style={{ maxWidth: "900px", margin: "0 auto" }}>{children}</div>
      </Content>
      <Footer
        style={{
          backgroundColor: "#101010",
          color: "#FFFFFF",
          fontSize: "16px",
        }}
      >
        <span
          style={{
            float: "left",
          }}
        >
          © {new Date().getFullYear()} Criz Games
        </span>
        <span style={{ float: "right" }}>
          <Space size="middle">
            <LinkNewTab to="mailto:comp.criz.games@gmail.com">
              <MailOutlined className="icon white" />
            </LinkNewTab>
            <LinkNewTab to="https://assetstore.unity.com/publishers/51178">
              <img
                src={unity}
                style={{
                  color: "white",
                  height: "24px",
                  transform: "translateY(-2px)",
                }}
              />
            </LinkNewTab>
            <LinkNewTab to="https://github.com/CrizGames/">
              <GithubFilled className="icon white" />
            </LinkNewTab>
          </Space>
        </span>
      </Footer>
    </Layout>
  );
};
Container.propTypes = {
  children: PropTypes.node.isRequired,
};

export default Container;
